.form-container {
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

label {
  display: block;
  margin: 0.75rem 0 0.25rem 0;
  color: var(--text-secondary, #374151);
  font-weight: 500;
}

input {
  width: 100%;
  padding: 8px;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  margin-bottom: 0.5rem;
  font-size: 1rem;
}
input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 2px rgba(59,130,246,0.14);
}
.error {
  color: #dc2626;
  margin: 0.5rem 0;
  border-left: 3px solid #dc2626;
  padding-left: 0.5rem;
  background: #fee2e2;
  border-radius: 4px;
  font-size: 0.95rem;
}
.success {
  color: #15803d;
  margin: 0.5rem 0;
  border-left: 3px solid #15803d;
  padding-left: 0.5rem;
  background: #d1fadf;
  border-radius: 4px;
  font-size: 0.95rem;
}
.btn-primary {
  background: #3b82f6;
  color: white;
  border: 1px solid #3b82f6;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
  transition: all .16s;
  cursor: pointer;
}
.btn-primary:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}
