.customer-detail-container {
  max-width: 750px;
  margin: 2.5rem auto;
  padding: 2rem;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}
.info-list {
  margin-bottom: 1.5rem;
  padding: 0.75rem 1.2rem;
  background: #f9fafb;
  border-radius: 8px;
}
.info-list > div {
  margin: 0.5rem 0;
}
.claims-table {
  width: 100%;
  border-collapse: collapse;
}
.claims-table th, .claims-table td {
  padding: 0.7rem;
  border-bottom: 1px solid #e5e7eb;
}
.claims-table thead {
  background: #f9fafb;
}
.table-row {
  transition: background 0.12s;
  cursor: pointer;
}
.table-row:hover {
  background: #f3f4f6;
}
.empty-state {
  color: #666;
  font-style: italic;
  text-align: center;
  margin: 1.2rem 0 0 0;
}
.btn-secondary {
  background: transparent;
  color: #3b82f6;
  border: 1px solid #3b82f6;
  border-radius: 6px;
  padding: 0.4rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  transition: all .16s;
  cursor: pointer;
}
.btn-secondary:hover {
  background: #3b82f6;
  color: #fff;
}
.error {
  color: #dc2626;
  margin-bottom: 1rem;
}
