.claim-detail-container {
  max-width: 700px;
  margin: 2.5rem auto;
  padding: 2rem;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.08);
}
.info-list {
  margin-bottom: 1.7rem;
  padding: 0.75rem 1.1rem;
  background: #f9fafb;
  border-radius: 8px;
}
.info-list > div {
  margin: 0.55rem 0;
  overflow-wrap: break-word;
}
.status-form {
  margin-bottom: 1.5rem;
  display: flex;
  align-items: flex-end;
  gap: 1.5rem;
}
.status-form label {
  margin-bottom: 0.3rem;
  font-weight: 500;
  color: #374151;
}
.status-form select {
  width: 170px;
  padding: 8px;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  font-size: 1rem;
}
.btn-primary {
  background: #3b82f6;
  color: white;
  border: 1px solid #3b82f6;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 0;
  transition: all .16s;
  cursor: pointer;
}
.btn-primary:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}
.success {
  color: #15803d;
  margin: 0.5rem 0;
  border-left: 3px solid #15803d;
  padding-left: 0.5rem;
  background: #d1fadf;
  border-radius: 4px;
  font-size: 0.95rem;
}
.error {
  color: #dc2626;
  margin-bottom: 1rem;
  border-left: 3px solid #dc2626;
  padding-left: 0.5rem;
  background: #fee2e2;
  border-radius: 4px;
  font-size: 0.95rem;
}
