.main-nav {
  background: #f9fafb;
  padding: 18px 0 12px 0;
  margin-bottom: 1.5rem;
  box-shadow: 0 2px 4px rgba(60,60,80, .06);
}
.main-nav nav {
  display: flex;
  gap: 2rem;
  justify-content: center;
  font-size: 1.08rem;
  font-weight: 500;
}
.main-nav a {
  color: #2563eb;
  text-decoration: none;
}
.main-nav a:hover {
  text-decoration: underline;
}
.app-content {
  min-height: 60vh;
}
body {
  background: #f3f4f6;
}
h2, h3 {
  color: #22223b;
  font-weight: 700;
  letter-spacing: 0.01em;
}
button {
  font-family: inherit;
}
