.list-container {
  max-width: 750px;
  margin: 2.5rem auto;
  padding: 2rem;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}
.claim-table {
  width: 100%;
  border-collapse: collapse;
}
.claim-table th, .claim-table td {
  padding: 0.75rem;
  border-bottom: 1px solid #e5e7eb;
}
.claim-table thead {
  background: #f9fafb;
}
.table-row {
  transition: background 0.12s;
  cursor: pointer;
}
.table-row:hover {
  background: #f3f4f6;
}
.filter-row {
  display: flex; gap: 1rem; align-items: center;
  margin-bottom: 1.3rem;
}
.empty-state {
  color: #666; font-style: italic; text-align: center;
  margin: 1.5rem 0 0 0;
}
.error {
  color: #dc2626;
  margin-bottom: 1rem;
}
.btn-primary {
  background: #3b82f6;
  color: white;
  border: 1px solid #3b82f6;
  border-radius: 6px;
  padding: 0.44rem 1.2rem;
  font-size: 0.97rem;
  font-weight: 500;
  transition: all .16s;
  cursor: pointer;
}
.btn-primary:hover {
  background: #2563eb;
}
.btn-primary:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}
