.list-container {
  max-width: 700px;
  margin: 2.5rem auto;
  padding: 2rem;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}
.customer-table {
  width: 100%;
  border-collapse: collapse;
}
.customer-table th, .customer-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid #e5e7eb;
}
.customer-table thead {
  background: #f9fafb;
}
.table-row {
  transition: background 0.12s;
  cursor: pointer;
}
.table-row:hover {
  background: #f3f4f6;
}
.empty-state {
  color: #666;
  font-style: italic;
  text-align: center;
  margin: 1.5rem 0 0 0;
}
.error {
  color: #dc2626;
  margin-bottom: 1rem;
}
